@import "partials/fonts";
@import "partials/settings";
@import "partials/media";

// Main

* {
	box-sizing: border-box;

	&:before,
	&:after {
		box-sizing: border-box;
	}
}

html,
body {
	margin: 0;
	width: 100%;
}

body {
	//font-family: $opensans;
	position: relative;
	line-height: 1.2;
	font-size: 16px;
	font-weight: 400;
	//color: $black;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}

a {
	color: inherit;
	text-decoration: none;
	transition: all 0.2s;

	&:hover,
	&:focus {
		text-decoration: none;
		outline: none;
		color: inherit;
	}
}

p {
	margin: 0;
	padding: 0;
}

img {
	max-width: 100%;
	height: auto;
}

input,
textarea,
button {
	outline: none;
	-webkit-appearance: none;
	resize: none;

	&:focus {
		outline: none;
	}
}
